<template>
	<div :style="{ padding: '0 80px', marginTop: '8px' }">
		<div style="margin: 50px 0 0 50px; width: 1000px;">
			<!-- 地区单选器 -->
			<div style="float: left;">
				<div style="margin:15px 20px 0 50px; float: left; font-size: medium;">地区</div>
				<div style="float: left;">
					<a-radio-group v-model="parms.aid" button-style="solid" size="large" @change="onChangeAreaSelect">
						<div style="margin: 10px; float: left;" v-for="item in areas">
							<a-radio-button :value="item.aid">
								{{item.name}}
							</a-radio-button>
						</div>
					</a-radio-group>
				</div>
			</div>
			<!-- 时间单选器 -->
			<div style="float: left;">
				<div style="margin:30px 20px 0 50px; float: left; font-size: medium;">时间</div>
				<div style="margin:15px 0 5px 0; float: left; width: 800px;">
					<a-radio-group v-model="movieDbParms.tid" button-style="solid" size="large" @change="onChangePeriodSelect">
						<div style="margin: 10px; float: left;" v-for="item in periods">
							<a-radio-button :value="item.tid">
								{{item.name}}
							</a-radio-button>
						</div>
					</a-radio-group>
				</div>
			</div>
			<!-- 类别单选器 -->
			<div style="float: left;">
				<div style="margin:30px 20px 0 50px; float: left; font-size: medium;">类别</div>
				<div style="margin:10px 0 50px 0; float: left; width: 700px;">
					<a-radio-group v-model="movieDbParms.gid" button-style="solid" size="large" @change="onChangeGenreSelect">
						<div style="margin: 10px; float: left;" v-for="item in genres">
							<a-radio-button :value="item.gid">
								{{item.name}}
							</a-radio-button>
						</div>
					</a-radio-group>
				</div>
			</div>
		</div>
		<div>
			<a-divider dashed />
		</div>
		<div :style="{ padding: '24px 0px 50px 100px', minHeight: '500px' , textAlign: 'center'}">
			<MovieCard :movies="movies" v-if="movies.length!=0"></MovieCard>
			<a-empty v-else/>
		</div>
		<div style="text-align: center; margin-bottom: 80px;" v-if="movies.length!=0">
		    <a-pagination show-quick-jumper v-model="movieDbParms.page" :total="movieDbParms.total" :defaultPageSize = "movieDbParms.limit" @change="onChangePage" />
		</div>
	</div>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from "vuex";
	import MovieCard from './MovieCard.vue'
	export default {
		data() {
			return {
				count : null,
				areas: [],
				genres: [],
				periods: [],
				parms: {
					aid: -1,
					gid: -1,
					tid: -1,
					page: 1,
					limit: 16,
				},
				movies: [],
			}
		},
		components:{
			MovieCard,
		},
		computed: {
			...mapState(['requestPath','movieDbParms'])
		},
		methods: {
			async getGenres() {
				let {
					data
				} = await this.$get(this.requestPath + 'getGenres');
				this.genres = data.data;
			},
			async getAreas() {
				let {
					data
				} = await this.$get(this.requestPath + 'getAreas');
				this.areas = data.data;
			},
			async getPeriods() {
				let {
					data
				} = await this.$get(this.requestPath + 'getPeriods');
				this.periods = data.data;
			},
			async getMovies() {
				let {
					data
				} = await this.axios.post(this.requestPath + 'select', this.movieDbParms);
				this.movies = data.data;
				if (this.movieDbParms.page == 1){
					this.movieDbParms.total = data.count;
				};
			},
			onChangeAreaSelect(e) {
				this.movieDbParms.page = 1;
				this.movieDbParms.aid = e.target.value;
				this.getMovies();
			},
			onChangePeriodSelect(e){
				this.movieDbParms.page = 1;
				this.movieDbParms.tid = e.target.value;
				this.getMovies();
			},
			onChangeGenreSelect(e) {
				this.movieDbParms.page = 1;
				this.movieDbParms.gid = e.target.value;
				this.getMovies();
			},
			onChangePage(pageNumber) {
				this.movieDbParms.page = pageNumber;
				this.getMovies();
			},
		},
		async created() {
			this.getAreas();
			this.getPeriods();
			this.getGenres();
			this.getMovies();
		},
	}
</script>

<style lang="scss">
</style>
